import React from 'react';
import styles from '../styles.module.scss';

function Pics({title, list}) {
  return (
    <div className={styles['pic-list']}>
      <h1>{title}</h1>
      <ul>
        {list.map((item) => (
          <li key={item.id} className={styles['img-box']}>
            <img src={item.img} alt=""/>
          </li>
        ))}
      </ul>
    </div>
  );
}
export default Pics;
